@media (max-width: 1290px) {
    .descCompany, .footerDescCompany {
        font-size: 14px;
        padding: 10px;
    }
    .nameCompany {
        font-size: 22px;
    }
    .subtitleCooperation, .btnCoop button {
        font-size: 14px;
    }
}
@media (max-width: 1122px) {
    .imageCompany img {
        max-width: 460px;
    }
    .nameCompany {
        font-size: 15px;
        padding: 5px;
    }
    .descCompany, .footerDescCompany {
        font-size: 14px;
        padding: 5px;
        font-weight: lighter;
    }
}
@media (max-width: 1002px) {
    .imageCompany img {
        max-width: 350px;
    }
    .nameCompany {
        font-size: 14px;
        padding: 0 10px;
    }
    .descCompany {
        margin-bottom: 5px;
    }
    .descCompany, .footerDescCompany {
        font-size: 14px;
        padding: 2px 10px;
        font-weight: lighter;
    }
    .footerDescCompany button, .titleCooperation button {
        width: 26px;
        height: 26px;
    }
    .titleCooperation h2 {
        font-size: 14px;
        font-weight: bolder;
    }
    .subtitleCooperation, .btnCoop button {
        font-size: 14px;
        font-weight: lighter;
    }
}
@media (max-width: 857px) {
    .textCompany {
        margin-left: 0;
    }
    .imageCompany {
        display: none;
    }
    .textCompany {
        background: #fff;
        border-radius: 10px;
        margin-top: 10px;
    }
    .nameCompany {
        padding: 10px 30px;
        margin-bottom: 10px;
        border-radius: 10px 0;
        background: #1A2238;
        border: none;
        color: #fff;
        position: relative;
        top: -2px;
    }
    .nameCompany h2 {
        font-size: 14px;
    }
    .descCompany, .footerDescCompany {
        font-size: 14px;
        padding: 20px 10px;
        font-weight: lighter;
        border-bottom: 1px solid #000;
        border-radius: 0;
    }
    .nameSection {
        margin: 0 0 25px 0;
        font-size: 20px;
    }
}

@media (max-width: 746px) {
    .flexFooter {
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
    }
    .wCol {
        width: 100%;
    }
    .wCol .backgroundCol, .nameCol {
        margin-top: 30px;
    }
}

@media (max-width: 661px) {
    .subtitleCooperation li:not(:last-child) {
        margin: 10px 10px 20px;
        padding-bottom: 15px;
    }
}

@media (max-width: 519px) {
    .titleCooperation {
        justify-content: flex-end;
    }
    .subtitleCooperation li {
        font-size: 12px;
    }
    .subtitleCooperation, .btnCoop button {
        font-size: 12px;
    }
    .footerDescCompany button, .titleCooperation button {
        width: 20px;
        height: 20px;
    }
    .titleCooperation h2 {
        font-weight: bolder;
        text-align: center;
        padding: 0 20px;
    }
    .descCompany, .footerDescCompany {
        font-size: 12px;
        padding: 12px;
    }
    .nameSection {
        font-size: 14px;
        margin-bottom: 20px;
    }
}

@media (max-width: 434px) {
    .descCompany, .footerDescCompany {
        font-size: 12px;
        padding: 10px 10px;
    }
    .textCompany {
        align-items: stretch;
    }
    .nameCompany {
        text-align: center;
    }
    .nameCompany {
        margin: 10px;
        border-radius: 10px;
    }
}