.iconBtnAll svg {
    width: 25px;
    height: 25px;
}

.iconBtnAll {
    color: #FF6A3D;
    cursor: pointer;
    transition: .5s all ease;
    display: flex;
}

.iconBtnAll:hover {
    transform: scale(1.2);
    color: #9DAAF2;
}

.btnSliderAll {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hidden {
    display: none;
}

/*   модалка   */

.modal {
    position: relative;
}

.modal button {
    position: absolute;
    top: -15px;
    right: -15px;
    background: #fff;
    font-size: 20px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    text-align: center;
    border: none;
    cursor: pointer;
}

.header__popup {
    background: #1A2238;
    color: #fff;
    font-weight: 600;
    text-align: center;
    padding: 10px 0;
}

.content__popup {
    padding: 30px 50px;
    border-bottom: 1px solid #1a2238;
}

.content__popup ul {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer__popup {
    text-align: center;
    font-size: 12px;
    padding: 10px;
    color: #1A2238;
}

.flip-card {
    perspective: 1000px;
    width: 200px;
    height: 200px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.5s;
}
.flip-card.flipped {
    transform: rotateY(180deg);
}
.flip-card-inner {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
}
.flip-card-front,
.flip-card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
}
.flip-card-front {
    background-color: #1A2238;
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
}
.flip-card-back {
    background-color: #fff;
    border: 1px solid #1A2238;
    color: #1A2238;
    transform: rotateY(180deg);
    padding: 10px 20px;
    border-radius: 10px;
}

.blockMagazineModal {
    height: 200px;
    border-radius: 10px;
    perspective: 1000px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.5s;
}
.blockMagazineModal:not(:last-child) {
    margin-right: 20px;
}
.modal .btnFlipCard {
    bottom: 0;
    right: 0;
    top: unset;
    color: #fff;
    background: transparent;

}
.modal .btnFlipCardBack {
    color: #1A2238;
}