@media (max-width: 857px) {
    .categories {
        top: 115px;
    }
}

@media (max-width: 445px) {
    .main .item.rowItem {
        height: 100px;
    }
    main .rowItem p {
        display: none;
    }
    main .rowItem .colorWhite {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}

@media (max-width: 768px) {
    .titleCardModal h2  {
        font-size: 18px;
    }
    .descriptionCardModal, .pol, .brandName, .magazine {
        font-size: 14px;
    }
    .priceCardModal {
        font-size: 18px;
    }
    header .presentation:before {
        display: none;
    }
}

@media (max-width: 650px) {
    .rowCardModal {
        flex-direction: column;
    }
    .colCardModal {
        margin-bottom: 20px;
    }
    .colCardModalRight {
        margin-left: 0;
        width: 100%;
    }
    .blockImageCardModal {
        width: 100%;
    }
    .blockImageCardModal img {
        width: 100%;
    }
}

@media (max-width: 519px) {
    .categories {
        top: 95px;
    }
}