.row {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    height: 100%;
}
.imageCompany {
    background: #fff;
    border-radius: 10px;
    display: flex;
}
.imageCompany img {
    max-width: 600px;
    height: auto;
    padding: 10px;
    border-radius: 20px;
}
.textCompany {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    margin-left: 20px;
}

.nameCompany {
    font-weight: 600;
    font-size: 26px;
    border-radius: 10px;
    padding: 10px;
}

.descCompany {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 600;
    border-radius: 10px;
    padding: 10px;
}

.footerDescCompany {
    font-size: 16px;
    font-weight: 600;
    display: flex;
    border-radius: 10px;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
}

.footerDescCompany button, .titleCooperation button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    transition: .5s all ease;
}

.titleCooperation button {
    position: absolute;
    left: 10px;
}
.footerDescCompany button:hover, .titleCooperation button:hover {
    background: #FF6A3D;
}

.cooperation {
    display: flex;
    flex-direction: column;
}

.titleCooperation {
    margin: 30px 0;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.titleCooperation h2 {
    font-size: 20px;
    font-weight: lighter;
}

.subtitleCooperation {
    font-size: 16px;
    font-weight: 600;
    border-radius: 10px;
    padding: 10px;
    list-style: none;
}

.subtitleCooperation li:not(:last-child) {
    margin: 30px 20px;
    padding-bottom: 20px;
}

.btnCoop{
    display: flex;
    align-items: center;
    justify-content: center;
}

.btnCoop button {
    cursor: pointer;
    border: none;
    background: #FF6A3D;
    padding: 10px 15px;
    border-radius: 10px;
    color: #fff;
    font-size: 16px;
    font-weight: lighter;
    transition: .5s all ease;
}

.btnCoop button:hover {
    background: #9DAAF2;
}

.formBlockAll {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formBlockAll form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
}


