@media (max-width: 1091px) {
    .desc {
        font-size: 14px;
    }
    .name {
        font-size: 16px;
        margin-bottom: 15px;
    }
    .blockText button {
        font-size: 14px;
    }
    .itemSliderLeft img, .itemSliderCenter img, .itemSliderRight img {
        max-width: 335px;
    }
}

@media (max-width: 900px) {
    .mask1, .mask3, .mask {
        height: 230px;
    }
    .itemSliderLeft img, .itemSliderCenter img, .itemSliderRight img {
        max-width: 230px;
    }
    .desc {
        font-size: 12px;
    }
    .blockText button {
        font-size: 12px;
    }
}
@media (max-width: 857px) {
    .mt25 {
        margin-top: 25px!important;
    }
    .mask1, .mask3, .mask {
        margin-bottom: 0;
    }

}
@media (max-width: 558px) {
    .itemSliderLeft img, .itemSliderCenter img, .itemSliderRight img {
        max-width: 170px;
    }
    .mask1, .mask3, .mask {
        height: 270px;
        margin-bottom: 20px;
    }
    .mask1 {
        margin-top: 20px;
    }
    .pDescL, .pDescR  {
        padding: 0 10px;
    }
    .desc span {
        display: none;
    }
    .blockText button {
        padding: 8px;
    }
    .iconBtnAll {
        width: 20px;
    }
}