.popupAddToCart {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: .5s all ease;
}

.textPopupAddToCart {
    padding: 10px 15px;
    border-radius: 10px;
    cursor: pointer;
    transition: .5s all ease;
}
.iconClose {
    opacity: 0;
    margin-right: 10px;
    border-radius: 50%;
    color: #FF6A3D;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: .5s all ease;
}

@keyframes animationBtnCloseModal {
    0% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.iconClose {
    font-size: 26px;
}
.popupAddToCart:hover .iconClose {
    animation: animationBtnCloseModal 1s forwards;
    transition: .5s all ease;
}