.linkBuyCard {
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #FF6A3D;
    border: none;
    transition: .5s all ease;
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    text-decoration: none;
    margin-top: 10px;
}

.descItem {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}