footer {
    margin-top: 50px;
}

.brandImage img {
    width: 150px;
}

.titleBrand {
    margin-top: 20px;
}

.pFooter {
    padding: 20px 0;
}

.wCol {
    width: 30%;
}

.bottomFooter {
    justify-content: center;
    display: flex;
    align-items: stretch;
    height: 100%;
    padding: 20px 0;
    text-align: center;
}

.nameCol {
    text-transform: uppercase;
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 18px;
}

.nameCol::after {
    content: '';
    position: absolute;
    width: 40px;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #FF6A3D;
}

.descriptionCol li {
    display: flex;
    align-items: center;
}

.descriptionCol li a {
    border: 1px solid transparent;
    padding-bottom: 5px;
    cursor: pointer;
    transition: .5s all ease;
}

.descriptionCol li a:hover {
    border-bottom: 1px solid #FF6A3D;
    color: #FF6A3D;
}

.descriptionCol li:not(:last-child) {
    margin-bottom: 10px;
}

.iconList {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
}

.iconList svg {
    font-size: 22px;
}

.socialNetworks ul {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 20px;
}

.socialNetworks ul li {
    display: flex;
    align-items: center;
    justify-content: center;
}

.socialNetworks ul li:not(:last-child) {
    margin-right: 20px;
}

.socialNetworks ul li a {
    cursor: pointer;
    transition: .5s all ease;
}

.socialNetworks ul li a:hover {
    color: #FF6A3D;
}

.socialNetworks ul li svg {
    font-size: 40px;
}