.blockForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.blockForm form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 10px;
}

.inpForm {
    width: 100%;
}

.inpForm:not(:last-child) {
    margin-bottom: 20px;
}

.inpForm input {
    width: 100%;
    padding: 10px;
}

.btnForm {
    cursor: pointer;
    border: none;
    background: #FF6A3D;
    padding: 10px 15px;
    border-radius: 10px;
    color: #fff;
    font-size: 16px;
    font-weight: lighter;
    transition: .5s all ease;
}

.btnForm:hover {
    background: #9DAAF2;
}

.error {
    font-size: 14px;
    color: red;
}

.headerForm {
    background: #1A2238;
    color: #fff;
    text-align: center;
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
}

.success {
    position: fixed;
    z-index: 100000;
    top: 50%;
}