@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,300;0,600;1,300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    font-family: 'Roboto Mono', monospace;
}

.popup-content {
    width: 80%!important;
}

.dark {
    background: #1A2238;
}

.day {
    background: #fff;
}

.bgDay {
    background: #fff;
}

.bgDark {
    background: #1A2238;
}

.borderDarkAll {
    border: 1px solid #1A2238;
}

.borderWhiteAll {
    border: 1px solid #fff;
}

.borderOrange {
    border: 1px solid #FF6A3D;
}

.colorOrange {
    color: #FF6A3D;
}

a {
    all: unset;
}

.mb0 {
    margin-bottom: 0!important;
}

.wrapper {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
}

.pt-20 {
    padding-top: 20px;
}

header {
    position: relative;
}

header .logo {
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
}

header .presentation {
    margin-top: 20px;
    background: url("components/bg.png") no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 500px;
    position: relative;
    transition: all 700ms ease;
    cursor: pointer;
}

header .presentation:hover {
    background: url("components/bg2.png") no-repeat;
    background-size: cover;
    background-position: top center;
    width: 100%;
    height: 500px;
    color: #000;
}



