/*    position: absolute;*/

/*}*/

header .presentation:hover .presentation:after {
    color: #fff;
}

header .presentation:before {
    content: url("../src/components/icon/logo2.svg");
    max-width: 80%;
    height: auto;
    top: 50px;
    left: 50px;
    position: absolute;
}

.colorWhite {
    color: #fff;
}
.colorDark {
    color: #1A2238;;
}

.borderDark {
    border-bottom: 1px solid #1a2238;
}
.borderDay {
    border-bottom: 1px solid #FF6A3D;
}



header .nav {
    float: right;
    list-style: none;
}

header .shop-cart-button {
    float: right;
    cursor: pointer;
    transition: color, transform 500ms ease;
    margin-top: 5px;
}

header .shop-cart-button:hover,
header .shop-cart-button.active {
    color: #FF6A3D;
    transform: scale(1.3);
}

header .shop-cart {
    position: absolute;
    top: 55px;
    right: 20px;
    width: 500px;
    background-color: #fff;
    -webkit-box-shadow: 6px 2px 8px 2px rgba(0, 63, 111, 0.23);
    -moz-box-shadow: 6px 2px 8px 2px rgba(0, 63, 111, 0.23);
    box-shadow: 6px 2px 8px 2px rgba(0, 63, 111, 0.23);
    z-index: 1000;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}
.shopActive {
    max-height: 501px;
    overflow: scroll;
}



header .shop-cart .item {
    display: flex;
    width: 100%;
    padding: 10px 0 10px 10px;
    position: relative;
    border-bottom: 1px solid #1A2238;
}

header .shop-cart .nothingTitle {
    padding: 20px 0 20px 20px;
    font-weight: 600;
}

header .shop-cart .sum {
    float: left;
    padding: 10px;
    font-weight: 600;
}

header .shop-cart .item .btnDeleteItem {
    position: absolute;
    right: 60px;
    top: 40%;
    color: #910000;
    transition: transform 500ms ease;
    cursor: pointer;
}

header .shop-cart .item .btnDeleteItem:hover {
    transform: scale(1.3);
}


header .shop-cart .item img {
    width: 30%;
    margin-right: 30px;
}

header .shop-cart .item .descItem h2 {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 10px;
}

header .shop-cart .item .descItem b {
    font-size: 16px;
    font-weight: 300;
}

header .nav li {
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    padding: 5px 10px;
}

header .nav li a {
    transition: .5s all ease;
    padding-bottom: 5px;
}



header .nav li a:hover {
    color: #FF6A3D;
    border-bottom: 1px solid #FF6A3D;
}

.categories div {
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
    transition: all 500ms ease;
    width: 100%;
}

.categories .categorie:hover,
.categories .categorie.active {
    border-bottom: 1px solid #ff6a3d;
    background-color: #ff6a3d;
}



main {
    margin-top: 20px;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item {
    width: 24%;
    margin-bottom: 50px;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5px;
    border-radius: 10px;
    transition: .5s all ease;
    cursor: pointer;
    border: 3px solid transparent;
}

main .item.rowItem {
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;
}

main .item.rowItem img {
    max-width: 30%;
}

main .item:hover {
    border: 3px solid #FF6A3D;
}

main .item img {
    max-width: 100%;
    border-radius: 10px;
    transition: transform 500ms ease;
    cursor: pointer;
}

main .item img:hover {
    transform: scale(1.05);
}

.itemRowCategory h2 {
    margin: 10px 0 0 10px;
    font-size: 18px;
}

.itemRowCategory p {
    margin: 5px 0 30px 10px;
    font-size: 14px;
}

.itemRowCategory b {
    margin: 0 0 0 10px;
}

.itemColCategory h2 {
    margin: 10px 0 0 20px;
}

.itemColCategory p {
    margin: 15px 0 30px 20px;
    font-size: 14px;
}

.itemColCategory b {
    margin: 0 0 0 20px;
}



main .item .AddToCart {
    height: 30px;
    width: 30px;
    border-radius: 5px;
    font-weight: 600;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    transition: .5s all ease;
    position: relative;
    border: none;
    display: flex;align-items: center;
}

main .item .AddToCartDay {
    background-color: #FF6A3D;
    color: #fff;
}
main .item .AddToCartDark {
    background-color: #fff;
    color: #1A2238;
}

main .item .AddToCartDay:hover {
    background-color: #1A2238;
}
main .item .AddToCartDark:hover {
    background-color: #FF6A3D;
    color: #fff;
}




@media (max-width: 1024px) {
    main .item {
        width: 33%;
    }
}



@media (max-width: 768px) {
    main .item {
        width: 49.3%;
    }
    .fixedMenu {
        position: fixed;
        z-index: 1;
        background: #fff;
        top: 0;
        width: 100%;
        padding: 20px 20px;
    }
    header {
        padding-top: 40px;
    }
    header .shop-cart {
        top: 50px;
        right: 20px;
        left: 20px;
        width: auto;
    }
    header .nav {
        display: none;
    }
    header .presentation {
        height: 320px;
    }
    header .presentation:hover {
        height: 320px;
    }
    header .presentation:after {
        font-size: 25px;
        top: 20px;
    }
    header .presentation:before {
        font-size: 15px;
        top: 50px;
    }
    .categories div {
        padding: 5px 10px;
        font-size: 14px;
    }
    header .logo {
        font-size: 14px;
    }
    header .nav li {
        font-size: 14px;
    }
    main .item h2 {
        font-size: 14px;
    }
    main .item p {
        font-size: 10px;
    }
    main .item b {
        font-size: 12px;
    }
    .popup-content {
        width: 80%!important;
    }
}

@media (max-width: 620px) {
    main .item {
        margin-bottom: 25px;
    }
    .blockCardBtn {
        bottom: 5px;
    }
}

@media (max-width: 484px) {
    .blockCardBtnRowCategory {
        align-items: flex-end;
    }
    .openModalCardBtn {
        height: 20px;
        line-height: 0;
    }
    main .item .AddToCart {
        height: 20px;
        width: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media (max-width: 450px) {
    .itemRowCategory h2 {
        margin: 10px 0 0 0;
    }
    .itemRowCategory p {
        margin: 5px 0 15px 0;
    }
    .itemRowCategory b {
        margin: 0;
    }
    main .item {
        width: 100%;
    }
}

@media (max-width: 414px) {
    header .presentation:after {
        top: 30px;
        left: 25px;
    }
    header .presentation:before {
        top: 60px;
        left: 25px;
    }
}

