.iconAccount svg {
    width: 20px;
    height: 20px;
    transition: .5s all ease;
}

.iconAccount svg:hover {
    transform: scale(1.3);
    fill: #b4b4b4;
}

.slider {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    overflow: hidden;
}

.itemSliderLeft img, .itemSliderCenter img, .itemSliderRight img {
    max-width: 500px;
    transition: .5s all ease;
    cursor: pointer;
}

.itemSliderLeft img:hover, .itemSliderCenter img:hover, .itemSliderRight img:hover {
    transform: scale(1.1);
}

.mask1, .mask3, .mask {
    overflow: hidden;
    transition: .5s all ease;
    display: flex;
    align-items: center;
    height: 300px;
    width: 100%;
    margin-bottom: 50px;
}

.mask1 {
    margin-top: 50px;
}

.itemSliderLeft {

}

.itemSliderRight {

}

.mask {
    border-radius:10px;
}

.mask1, .mask3 {
    border-radius:10px;
}

.blockText {
    height: 100%;
    display: flex;
    align-items: stretch;
    justify-content: space-around;
    flex-direction: column;
}

.pDescL {
    padding: 0 30px;
}

.pDescR {
    padding: 0 30px;
}

.desc {
    font-size: 16px;
}

.name {
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 40px;
}

.blockText button {
    padding: 10px 15px;
    border-radius: 5px;
    background-color: #FF6A3D;
    border: none;
    transition: .5s all ease;
    cursor: pointer;
    font-size: 16px;
}

.blockText button {
    color: #fff;
    text-decoration: none;
}

.blockText button:hover {
    background-color: #9DAAF2;
    color: #fff;
}

.btnUp {
    position: fixed;
    bottom: 30px;
    right: 30px;
    cursor: pointer;
    width: 35px;
    height: 35px;
    background: #FF6A3D;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: .5s all ease;
    z-index: 9999;
    color: #fff;
    border: 1px solid #FF6A3D;
}

.btnUp:hover {
    background: #fff;
    color: #1A2238;
    border: 1px solid #1A2238;
}

.partnersImg {
    margin-top: 50px;
}

.partnersImg img {
    height: 50px;
    margin: 0 50px;
}

