.changeThemes {
    display: flex;
    align-items: center;
    position: fixed;
    width: 60px;
    height: 30px;
    bottom: 30px;
    border-radius: 10px;
    margin-left: 0!important;
    left: 30px;
    z-index: 9999;
}

.darkCheckbox {
    background: #fff;
}
.dayCheckbox {
    background: #FF6A3D;
}

.circle {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    transition: 1s all ease;
}

.leftCheckbox {
    background: #fff;
    left: 5px;
}

.rightCheckbox {
    background: #1A2238;
    right: 5px;
}

