.nameSection {
    color: #fff;
    font-size: 26px;
    margin: 50px 0;
}

.categoriesBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.changeSetka button {
    background: #FF6A3D;
    border: none;
    border-radius: 5px;
    padding: 5px;
}

.changeSetka {
    display: flex;
    align-items: center;
}

.changeSetka .colSetka {
    margin-right: 10px;
}

.changeSetka .rowSetka, .changeSetka .colSetka {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    cursor: pointer;
    transition: .5s all ease;
}
.changeSetka .rowSetka:hover, .changeSetka .colSetka:hover {
    background: #fff;
    color: #1A2238;
    transform: scale(1.1);
}

.categories {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 150px;
    background: #fff;
    z-index: 999;
    border: 1px solid #1A2238;
}

.btnOpenCategories {
    width: 10%;
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
}