.rowCardModal {
    display: flex;
    padding-bottom: 20px;
}
.colCardModal {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blockImageCardModal img {
    max-width: 100%;
    height: auto;
}
.blockColorsCard {
    display: flex;
    align-items: center;
    margin-top: 20px;
}
.blockColor:not(:last-child) {
    margin-right: 10px;
}
/*.w30 {*/
/*    width: 40%;*/
/*}*/
.w70 {
    width: 60%;
}
.openModalCardBtn {
    margin-right: 20px;
    border-radius: 5px;
    border: none;
    height: 30px;
    padding: 0 10px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    transition: .5s all ease;
}

.mr10 {
    margin-right: 10px;
}

.openModalCardBtnDay {
    background-color: #FF6A3D;
    color: #fff;
}
.openModalCardBtnDark {
    background-color: #fff;
    color: #1A2238;
}

.openModalCardBtnDay:hover {
    background-color: #1A2238;
}
.openModalCardBtnDark:hover {
    background-color: #FF6A3D;
    color: #fff;
}
.blockCardBtn {
    display: flex;
    position: absolute;
    bottom: 10px;
    right: 10px;
}
.blockCardBtnRowCategory {
    display: flex;
    position: absolute;
    bottom: 5px;
    right: 10px;
}

.colCardModalRight {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.descriptionCardModal {
    margin: 10px 0 10px;
    font-size: 16px;
}

.priceCardModal {
    display: flex;
    font-size: 24px;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
}

.AddToCart {
    border-radius: 5px;
    font-size: 16px;
    padding: 5px 10px;
    font-weight: 600;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    transition: .5s all ease;
    position: relative;
    border: none;
}

.AddToCartDay {
    background-color: #FF6A3D;
    color: #fff;
}

.AddToCartDark {
    background-color: #FF6A3D;
    color: #fff;
}

.AddToCartDay:hover {
    background-color: #FF6A3D;
}

.AddToCartDark:hover {
    background-color: #FF6A3D;
    color: #fff;
}

.blockColor {
    cursor: pointer;
    transition: 1s all ease;
    position: relative;
    border: 1px solid #000;
}
.blockColor:hover {
    top: -3px;
}

.blockColor img {
    width: 60px;
    height: auto;
}
.p20 {
    padding: 20px;
}

.footer__popupCard {
    text-align: center;
    font-size: 12px;
    padding-top: 20px;
    color: #1A2238;
    border-top: 1px solid #FF6A3D;
}

.BlockColorBlack {
    background: #000;
}

.BlockColorWhite {
    background: #fff;
    border: 1px solid #1A2238;
}

.btnBuy {

}

.brandName {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.brandName i {
    margin-left: 5px;
}

.pol {
    display: flex;
    align-items: center;
}

.pol i {
    margin-left: 5px;
}

.magazine {
    margin-top: 10px;
}

.magazine a {
    color: #1A2238;
    font-style: italic;
}